<script>
import { Icon } from "@iconify/vue2";
import reportsService from '@/services/reportsService';

export default {
  name: 'DashboardHome',
  props: {
   pengguna: {
     type: Object,
     required: true
   }
 },
  components: {
    Icon
  },
  data() {
    return {
      statistics: {
        totalActiveReports: 0
      },
      statisticsLoading: false,
      currentUser: {
        name: 'John Doe',
        role: 'admin' // 'reporter', 'admin', 'super_admin'
      },
      
      quickActions: [
        {
          id: 'create-report',
          label: 'Buat Laporan Baru',
          description: 'Laporkan dugaan pelanggaran',
          icon: 'mdi:file-document-plus',
          iconBg: 'bg-blue-500',
          action: 'navigate',
          target: '/dashboard/create-report',
          roles: ['reporter']
        },
        {
          id: 'review-reports',
          label: 'Review Laporan',
          description: 'Tinjau laporan yang masuk',
          icon: 'mdi:clipboard-check',
          iconBg: 'bg-green-500',
          action: 'navigate',
          target: '/dashboard/pending-reports',
          roles: ['admin', 'super_admin']
        },
        {
          id: 'view-analytics',
          label: 'Lihat Statistik',
          description: 'Analisis data laporan',
          icon: 'mdi:chart-line',
          iconBg: 'bg-purple-500',
          action: 'navigate',
          target: '/dashboard/analytics',
          roles: ['admin', 'super_admin']
        },
        {
          id: 'manage-users',
          label: 'Kelola Pengguna',
          description: 'Tambah atau edit pengguna',
          icon: 'mdi:account-cog',
          iconBg: 'bg-orange-500',
          action: 'navigate',
          target: '/dashboard/users',
          roles: ['super_admin']
        }
      ],
      
      recentActivities: [
        {
          id: 1,
          title: 'Laporan baru diterima',
          description: 'Laporan dugaan korupsi dari pengguna anonymous',
          time: '2 jam yang lalu',
          icon: 'mdi:file-document-plus',
          iconBg: 'bg-blue-500',
          status: 'Baru'
        },
        {
          id: 2,
          title: 'Laporan telah direview',
          description: 'Laporan #WBS-2024-001 telah selesai ditinjau',
          time: '5 jam yang lalu',
          icon: 'mdi:check-circle',
          iconBg: 'bg-green-500',
          status: 'Selesai'
        },
        {
          id: 3,
          title: 'Pengguna baru terdaftar',
          description: 'Admin baru telah bergabung dengan sistem',
          time: '1 hari yang lalu',
          icon: 'mdi:account-plus',
          iconBg: 'bg-purple-500',
          status: 'Aktif'
        }
      ]
    }
  },
  
  computed: {
    stats() {
      return [
        {
          id: 'my-reports',
          label: 'Laporan Saya',
          value: '5',
          change: '+2',
          trend: 'up',
          icon: 'mdi:file-document',
          iconBg: 'bg-blue-500',
          roles: ['reporter']
        },
        {
          id: 'total-reports',
          label: 'Total Laporan',
          value: this.statisticsLoading ? '...' : this.statistics.totalActiveReports.toString(),
          change: '+12',
          trend: 'up',
          icon: 'mdi:file-document-multiple',
          iconBg: 'bg-green-500',
          roles: ['admin', 'super_admin']
        },
        {
          id: 'pending-reports',
          label: 'Menunggu Review',
          value: '8',
          change: '-3',
          trend: 'down',
          icon: 'mdi:clock-outline',
          iconBg: 'bg-yellow-500',
          roles: ['admin', 'super_admin']
        },
        {
          id: 'resolved-reports',
          label: 'Laporan Selesai',
          value: '92',
          change: '+15',
          trend: 'up',
          icon: 'mdi:check-circle',
          iconBg: 'bg-blue-500',
          roles: ['admin', 'super_admin']
        },
        {
          id: 'total-users',
          label: 'Total Pengguna',
          value: '1,284',
          change: '+24',
          trend: 'up',
          icon: 'mdi:account-group',
          iconBg: 'bg-purple-500',
          roles: ['super_admin']
        }
      ];
    },

    filteredStats() {
      return this.stats.filter(stat => 
        stat.roles.includes(this.currentUser.role)
      );
    },
    
    filteredQuickActions() {
      return this.quickActions.filter(action => 
        action.roles.includes(this.currentUser.role)
      );
    }
  },
  
  methods: {
    getRoleLabel(role) {
      const labels = {
        'reporter': 'Pelapor',
        'admin': 'Administrator',
        'super_admin': 'Super Administrator'
      };
      return labels[role] || 'Pengguna';
    },
    
    getRoleIcon(role) {
      const icons = {
        'reporter': 'mdi:account',
        'admin': 'mdi:shield-account',
        'super_admin': 'mdi:crown'
      };
      return icons[role] || 'mdi:account';
    },
    
    getStatusClass(status) {
      const classes = {
        'Baru': 'bg-blue-100 text-blue-700',
        'Selesai': 'bg-green-100 text-green-700',
        'Aktif': 'bg-purple-100 text-purple-700',
        'Pending': 'bg-yellow-100 text-yellow-700'
      };
      return classes[status] || 'bg-gray-100 text-gray-700';
    },
    
    handleQuickAction(action) {
      if (action.action === 'navigate') {
        this.$router.push(action.target);
      }
      // Handle other action types here
    },

    async fetchStatistics() {
      this.statisticsLoading = true;
      try {
        const response = await reportsService.getReportsStatistics();
        if (response.success) {
          this.statistics = response.data;
        }
      } catch (error) {
        console.error('Error fetching statistics:', error);
      } finally {
        this.statisticsLoading = false;
      }
    }
  },

  mounted() {
    this.fetchStatistics();
  }
}
</script>

