import api from './api';

class ReportsService {
  /**
   * Get all reports   /**
   * Update an existing report
   */
  async updateReport(id, reportData) {
    try {
      const response = await api.put(`/reports/${id}`, reportData);
      
      if (response.data.success) {
        return {
          success: true,
          report: response.data.data,
          message: response.data.message || 'Report updated successfully'
        };
      } else {
        throw new Error(response.data.message || 'Failed to update report');
      }
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Get all reports for the current user
   */
  async getMyReports(page = 0, size = 10) {
    try {
      const response = await api.get('/reports/my', {
        params: { page, size }
      });
      
      // Return the complete response data structure
      if (response.data.success) {
        return {
          content: response.data.data.content,
          pagination: {
            page: response.data.data.page,
            size: response.data.data.size,
            totalElements: response.data.data.totalElements,
            totalPages: response.data.data.totalPages,
            first: response.data.data.first,
            last: response.data.data.last,
            empty: response.data.data.empty
          },
          message: response.data.message
        };
      } else {
        throw new Error(response.data.message || 'Failed to fetch reports');
      }
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Get all reports for admin users
   */
  async getAllReports(page = 0, size = 10, sortBy = 'createdAt', sortDir = 'desc') {
    try {
      const response = await api.get('/reports', {
        params: { page, size, sortBy, sortDir }
      });
      
      // Return the complete response data structure
      if (response.data.success) {
        return {
          content: response.data.data.content,
          pagination: {
            page: response.data.data.page,
            size: response.data.data.size,
            totalElements: response.data.data.totalElements,
            totalPages: response.data.data.totalPages,
            first: response.data.data.first,
            last: response.data.data.last,
            empty: response.data.data.empty
          },
          message: response.data.message
        };
      } else {
        throw new Error(response.data.message || 'Failed to fetch all reports');
      }
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Get a specific report by ID
   */
  async getReport(id) {
    try {
      const response = await api.get(`/reports/${id}`);
      
      if (response.data.success) {
        return {
          report: response.data.data,
          message: response.data.message
        };
      } else {
        throw new Error(response.data.message || 'Failed to fetch report');
      }
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Create a new report
   */
  async createReport(reportData) {
    try {
      const response = await api.post('/reports', reportData);
      
      if (response.data.success) {
        return {
          success: true,
          report: response.data.data,
          message: response.data.message || 'Report created successfully'
        };
      } else {
        throw new Error(response.data.message || 'Failed to create report');
      }
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Update an existing report
   */
  async updateReport(id, reportData) {
    try {
      const response = await api.put(`/reports/${id}`, reportData);
      return response.data;
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Delete a report
   */
  async deleteReport(id) {
    try {
      const response = await api.delete(`/reports/${id}`);
      
      if (response.data.success) {
        return {
          success: true,
          message: response.data.message || 'Report deleted successfully'
        };
      } else {
        throw new Error(response.data.message || 'Failed to delete report');
      }
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Upload attachment for a report
   */
  async uploadAttachment(reportId, file) {
    try {
      const formData = new FormData();
      formData.append('attachment', file);
      
      const response = await api.post(`/reports/${reportId}/attachment`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      return response.data;
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Add evidence to a report
   */
  async addEvidence(reportId, evidenceData) {
    try {
      const response = await api.post(`/reports/${reportId}/evidence`, evidenceData);
      return response.data;
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Get reports statistics
   */
  async getReportsStatistics() {
    try {
      const response = await api.get('/reports/statistics');
      return response.data;
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Search reports
   */
  async searchReports(query, filters = {}) {
    try {
      const params = {
        q: query,
        ...filters
      };
      const response = await api.get('/reports/search', { params });
      return response.data;
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Update report status
   */
  async updateReportStatus(reportId, status, reason = "Tidak ada") {
    try {
      const response = await api.patch(`/reports/${reportId}/status`, { 
        status,
        reason
      });
      return response.data;
    } catch (error) {
      throw this.handleError(error);
    }
  }

  /**
   * Handle API errors
   */
  handleError(error) {
    if (error.response) {
      // Server responded with error status
      const message = error.response.data?.message || `Server error: ${error.response.status}`;
      return new Error(message);
    } else if (error.request) {
      // Request was made but no response received
      return new Error('Tidak dapat terhubung ke server. Pastikan server berjalan di http://localhost:8080');
    } else {
      // Something else happened
      return new Error(`Error: ${error.message}`);
    }
  }
}

export default new ReportsService();